import React, { useEffect, useRef, useState } from "react";
import { Home } from '../features/index'
import { finished as FinishedImage } from '../assets'
import '../index.css'
import { makeStyles } from '@material-ui/core/styles';

/*CHAT FILES*/
import io from "socket.io-client"
import "./../chat.css"

import {isMobile} from 'react-device-detect';
/*CHAT FILES*/


const HomeScreen = () => {
    
    const useStyles = makeStyles((theme) => ({
        finishedBanner: {
            backgroundImage: `url("${FinishedImage}")`,
            width: "100vw",
            height: "100vh",
            backgroundSize: "contain",
            backgroundPosition: "center",
            backgroundRepeat: "no-repeat"
        }
    }));
      
    const [timedOut, setTimedOut] = useState(false);
    const [fullscreen, setFullscreen] = useState(false);
    const [dateEvent, setDateEvent] = useState(null);
    const [earlyTime, setEarlyTime] = useState(null);
    const [finished, setFinished] = useState(false);
    const [error, setError] = useState(null);
    
    const classes = useStyles();

    /*CHAT FILES*/
    const [ state, setState ] = useState({idmessage:"", message: "", name: "" })
    const [chat, setChat] = useState([])
    const [currentCode, setCurrentCode] = useState(0);
    const [scrollMessage, setScrollMessage] = useState(null);
    const socketRef = useRef()
    let openChat = false;
    let btnChatName = "Abrir chat";
    let currentcode = 0;
    const videoContainer = document.querySelector(".videoContainer");
    /*CHAT FILES*/
    
    useEffect(() => {
            //window.location.pathname.replace(/\/$/, "") + "/event.json"
        fetch("/event.json")
            .then(res => res.json())
            .then(dataResult => {
    
                let data = dataResult.events[0];

                console.log("DATA: ",data);
            
                if (data.end_time) {
                    let diff = new Date(data.end_time).getTime() - Date.now();
                    console.log(data.end_time, " -- ", window.location.pathname.replace(/\/$/, "") + "/event.json")
                    if (diff <= 0) return setFinished(true)
                }
    
                let diff = new Date(data.date).getTime() - Date.now() - 1000 * (data.early_time || 0);
                if (data.fullscreen == true) setFullscreen(true);
                if (diff <= 0) return setTimedOut(true)
            
                setEarlyTime(data.early_time);
                setDateEvent(data.date);
            
            })
            .catch(err => {
                console.error(err);
                setError("Ha habido un error inesperado. Prueba de nuevo ms tarde")
            })
        
            /*CHAT FILES*/
            socketRef.current = io.connect(window.location.hostname == "localhost" ? "localhost:4000" : "/")
            socketRef.current.on("message", ({ idmessage, name, message, id }) => {
                setChat(chat => [...chat, { idmessage, name, message, id }])
                
            })
            return () => socketRef.current.disconnect()

            /*CHAT FILES*/
    
    }, []);
    
    if (finished) {
        return <div className={classes.finishedBanner}></div>
    }

    /*CHAT FILES*/
    const onTextChange = (e) => {
        setState({ ...state, [e.target.name]: e.target.value })
    }

    const onMessageSubmit = (e) => {

        setScrollMessage(document.querySelector(isMobile ? ".messagesContainerMobile":".messagesContainer"));

        setCurrentCode(socketRef.current.id);

        const {idmessage, name, message } = state

        socketRef.current.emit("message", { idmessage, name, message }, (e) => {
            console.log("enviado");
        })
        e.preventDefault()
        
        setTimeout(function () { setState({ idmessage: "", message: "", name }); }, 100);
        
    }

    const renderChat = () => {
        
        return chat.map(({ idmessage, name, message,id }, index) => (
            <div className={ id === socketRef.current.id ? isMobile?"messageItemMobile localMobile":"messageItem local" : isMobile?"messageItemMobile clientMobile":"messageItem client" } key={index}>
                
                <p><b>{name}:</b> <span>{message}</span></p>
                { index >= chat.length-1 ? autoScroll() : null }
            </div>
        ))
        
    }

    const autoScroll = () => {
        if (scrollMessage != null) {
            setTimeout(function(){ scrollMessage.scrollTop = 1000000; }, 100);
        }
    }

    
    const openCloseChat = () => { 
        let videoContainer = document.querySelector(".videoContainer");
        const chatContainer = document.getElementById("ChatContainer");
        const BtnChat = document.querySelector(".btnChat");
        
        if (openChat) {

            openChat = false

            if (isMobile) {
                chatContainer.style["right"] = "-25%";
                BtnChat.innerHTML = "Abrir Chat";
                videoContainer.style["width"] = "100%";
            } else {
                chatContainer.style["right"] = "-15%";
                BtnChat.innerHTML = "Abrir Chat";
                videoContainer.style["width"] = "100%";
            }
            
        } else {
            openChat = true;

            if (isMobile) {
                chatContainer.style["right"] = "0px";
                BtnChat.innerHTML = "Cerrar Chat";
                videoContainer.style["width"] = "75%";
            } else {
                chatContainer.style["right"] = "0px";
                BtnChat.innerHTML = "Cerrar Chat";
                videoContainer.style["width"] = "85%";
            }
            
        }
    }
    /*CHAT FILES*/
    
    if (fullscreen && timedOut) {
        return <div style={{
            height: "100vh",
            width: "100vw",
        }}>
            <div id="ChatContainer" className={ isMobile?"chatContainerMobile":"chatContainer"}>
                <button className="btnChat" onClick={ openCloseChat }>
                    { btnChatName }
                </button>
                <div className="container">
                    <div className={ isMobile?"messagesContainerMobile":"messagesContainer"}>
                        <h1>Mensajes</h1>
                        {renderChat()}
                    </div>
                    <div className={ isMobile?"userDataContainerMobile":"userDataContainer"}>
                        <form onSubmit={onMessageSubmit}>
                            <div className="name-field">
                                <input autoComplete="off" placeholder="Ingrese su nombre" name="name" onChange={(e) => onTextChange(e)} value={state.name} label="Name" />
                            </div>

                            <div className="message-field">
                                <input autoComplete="off" placeholder="Mensaje" name="message" onChange={(e) => onTextChange(e)} value={state.message} label="Name" />
                            </div>
                            <input className="btnForm" type="submit" value="Enviar" />
                        </form>
                    </div>
                </div>
            </div>
            <iframe
                src="https://vimeo.com/event/935847/embed"
                frameBorder="0"
                className="videoContainer"
                allow="autoplay; fullscreen; picture-in-picture"
                allowFullScreen
                style={{ position: "absolute", top: 0, left: 0 }} />
        </div>
    }
    
    return (
        <Home onTimeout={() => setTimedOut(true)} timedOut={timedOut} dateEvent={dateEvent} error={error} earlyTime={earlyTime} />
    );
}

export default HomeScreen