import { makeStyles } from "@material-ui/core/styles";
import { vh } from "../../viewheight";
import { textByteFooterDesktop, textByteFooterMobile } from '../../assets/index';

const useStyles = makeStyles((theme) => ({
    background: {
        width: `100vw`,
        height: vh(50)
    },
    cover_page: {
        width: `100vw`,
        height: vh(50),
        objectFit: `cover`
    },
    container: {
        overflow: `hidden`,
        height: '100%',
        width: `100vw`,
        backgroundSize: 'cover',
        backgroundRepeat:'none'
    },
    operacion_tormenta: {
        position: `absolute`,
        bottom: `min(25vw, ${vh(25)})`,
        right: `min(18vw, ${vh(18)})`,
        width: `min(18vw, ${vh(18)})`,
        zIndex: 3,
        ["@media (orientation: portrait)"]: {
            position: `unset`,
            width: `min(20vw, ${vh(25)})`,
            margin: `unset`
        }
    },
    logo: {
        position: `fixed`,
        resizeMode: `contain`,
        top: vh(4),
        right: vh(4),
        width: `min(16vw,${vh(12)})`,
        zIndex: 2,
        ["@media (orientation: portrait)"]: {
            top: `4vw`,
            right: `4vw`,
        },
    },
    description: {
        position: `absolute`,
        right: `2vw`,
        bottom: vh(10),
        width: `22vw`,
        fontFamily: `'UnityHeadlineBold', Arial`,
        textAlign: `center`,
        color: `#FFFFFF`,
        fontSize: `calc(0.5vw + ${vh(0.5)} + 1vmin)`,
        padding: `calc(0.4vw + ${vh(0.4)} + 2vmin)`,
        border: `white solid 1px`,
        zIndex: 2,
        ["@media (orientation: portrait)"]: {
            left: 0,
            right: 0,
            bottom: vh(5),
            margin: `auto`,
            width: `auto`,
            height: `auto`,
            maxWidth: `65vw`,
            maxHeight: vh(8),
        },
    },
    subtitle: {
        width: `min(43vw, ${vh(70)})`,
        ["@media (orientation: portrait)"]: {
            width: `min(60vw, ${vh(60)})`,
        }
    },
    title: {
        position: `absolute`,
        resizeMode: `contain`,
        top: vh(8),
        left: 0,
        right: 0,
        marginLeft: `auto`,
        marginRight: `auto`,
        height: `15vw`,
        zIndex: 2,
        ["@media (orientation: portrait)"]: {
            height: `min(30vw, ${vh(28)})`,
            top: vh(8),
            left: 0,
            right: 0,
        }
    },
    titleMobile: {
        position: `absolute`,
        resizeMode: `contain`,
        top: `15vh !important`,
        left: 0,
        right: 0,
        marginLeft: `5vw`,
        marginRight: `auto`,
        height: `15vw`,
        zIndex: 2,
        ["@media (orientation: portrait)"]: {
            height: `min(30vw, ${vh(28)})`,
            top: vh(8),
            left: 0,
            right: 0,
        }
    },
    countDownMainWrapper: {
        top: `45%`,
        transform: `translateY(-50%)`,
        position: `absolute`,
        left: `0px`,
        right: `0px`,
        margin:`0px auto`
    },
    footerText: {
        backgroundImage: `url(` + textByteFooterDesktop + `)`,
        backgroundSize: "contain",
        width: `55vw`,
        height: `5.80vw`,
        backgroundRepeat: `no-repeat`,
        position: `absolute`,
        bottom: `1vw`,
        left: `0vw`,
        right: `0vw`,
        margin:`0 auto`,
    },
    footerTextMobile: {
        backgroundImage: `url(` + textByteFooterMobile + `)`,
        backgroundSize: "contain",
        width: `95vw`,
        height: `16vw`,
        backgroundRepeat: `no-repeat`,
        position: `absolute`,
        bottom: `20vw`,
        left: `0vw`,
        right: `0vw`,
        margin:`0 auto`,
    },
    imageDescription: {
        width: `min(76vw, ${vh(45)})`,
        height: `min(4.5vw, ${vh(45)})`,
        aspectRatio: `10 / 2`,
        alignSelf: `flex-end`,
        padding: `min(2vw, ${vh(2)})`,
        borderRadius: `3px`,
        border: `solid 2px #ffffff57`,
        marginRight: `20px`,
        marginTop: `-${vh(14)}`,
        [theme.breakpoints.down('md')]: {
            alignSelf: `center`,
            width: `min(50vw, ${vh(29)})`,
            marginRight: `0px`,
            marginTop: `0px`,
        }
    },

    mobileTitle: {
        width: `min(100vw, ${vh(110)})`,
    },
    error: {
        fontFamily: `'UnityHeadlineBold', Arial`,
        color: `white`,
        position: `absolute`,
        bottom: vh(24),
        width: `59vw`,
        /* min-width: 300px; */
        textAlign: `center`,
        left: `50vw`,
        fontSize: `min(${vh(4)}, 4vw)`,
        transform: `translateX(-50%)`
    },
    leftContainer: {
        position: "relative",
        width: `10vw`,
        height: `10vw`,
        marginLeft: `3vw`,
        marginTop: `3vw`,
        float: 'left',
        backgroundSize: '100% 100%',
        backgroundRepeat:'none'
    },
    leftContainerMobile: {
        position: "absolute",
        width: `40vw`,
        height: `40vw`,
        left: `0px`,
        right: `0px`,
        top:`15vw`,
        margin:`0px auto`,
        backgroundSize: '100% 100%',
        backgroundRepeat:'none'
    },
    rightContainer: {
        position: "relative",
        width: `10vw`,
        height: `5vw`,
        marginRight: `3vw`,
        marginTop: `3vw`,
        float: 'right',
        backgroundSize: '100% 100%',
        backgroundRepeat:'none'
    },
    rightContainerMobile: {
        position: "absolute",
        width: `18vw`,
        height: `10vw`,
        right: `5vw`,
        bottom:`5vw`,
        backgroundSize: '100% 100%',
        backgroundRepeat:'none'
    },
    rightBG: {
        position: 'absolute',
        height: vh(90),
        width: `40vw`,
        bottom: 0,
        backgroundSize: '100% 100%',
        backgroundRepeat: 'none',
        right:`-12vw`
    },
    rightBGMobile: {
        position: 'absolute',
        height: `150vw`,
        width: `140vw`,
        bottom: 0,
        backgroundSize: '100% 100%',
        backgroundRepeat: 'none',
        right:`-80vw`
    },
    videoBackground: {
        position: 'fixed',
        right: 0,
        bottom: 0,
        width: '100%',
        height:'auto'
    },
    videoBackgroundMobile: {
        position: 'fixed',
        bottom: 0,
        width: '100%',
        height:'auto'
    }
}));

export { useStyles }