import { makeStyles } from '@material-ui/core/styles';
import zIndex from '@material-ui/core/styles/zIndex';

const useStyles = makeStyles((theme) => ({
    formContainer: {
        height:'100%'
    },
    forLabel: {
        color:'#FFF'
    },
    formcoca: {
        position: 'absolute',
        left: '0px',
        right: '0px',
        margin: '0px auto',
        width: '30vw',
        top: '40%',
        transform: 'translateY(-50%)'
    },
    formcocaMobile: {
        position: 'absolute',
        left: '0px',
        right: '0px',
        margin: '0px auto',
        width: '80vw',
        top: '45%',
        transform: 'translateY(-50%)'
    },
    alertMessage: {
        marginTop: '10px',
        zIndex:'30'
    }
}))

export { useStyles }