import { makeStyles } from '@material-ui/core/styles';
import { vh } from '../../../viewheight';
import { bgByteCounter, textByteBottom } from '../../../assets/index';

const useStyles = makeStyles((theme) => ({
  containerCircle:{
    borderRadius: `50%`,
    width: `15vw`,
    height: `15vw`,
    border:`2px solid white`,
    margin: `auto`,
  },
  circle:{
    background: `#ffffff`,
    borderRadius: `50%`,
    width: `13vw`,
    height: `13vw`,
    display:`table`,
    margin:`auto`
  },
  containerText:{
    display: `table-cell`,
    verticalAlign: `middle`,
    textAlign: `center`,
    fontSize: `2rem`,
  },
  outer : {
    width: _ => `min(14vw, ${vh(14)})`, /* You can define it by % also */
    height: _ => `min(25vw, ${vh(25)})`, /* You can define it by % also */
    position: `relative`,
    marginRight: `2vw`,
    marginLeft: `2vw`,
},
  outerMobile: {
    width: _ => `min(19vw, ${vh(14)})`, /* You can define it by % also */
    height: _ => `min(12vw, ${vh(25)})`, /* You can define it by % also */
    position: `relative`,
    marginRight: `2vw`,
    marginLeft: `1vw`,
  },
inner :{
  top: `10%`, left: `10%`, /* of the container */
  width: `80%`, /* of the container */
  height: `50%`, /* of the container */
  position: `relative`,
  borderRadius: `50%`,
  display: `table`,
  },
  bottomTextContainer: {
    position: `relative`,
    width: `100%`,
    height: `5.80vw`,
    paddingTop:`16.3vw`
  },
  bottomTextContainerMobile: {
    position: `relative`,
    width: `100%`,
    height: `5.80vw`,
    paddingTop:`34vw`
  },
  bottomText: {
    backgroundImage: `url(` + textByteBottom + `)`,
    backgroundSize: "contain",
    width: `35vw`,
    height: `5.80vw`,
    backgroundRepeat: `no-repeat`,
    position: `absolute`,
    right: `0px`,
    left: `0px`,
    margin:`0px auto`
  },
  bottomTextMobile: {
    backgroundImage: `url(` + textByteBottom + `)`,
    backgroundSize: "contain",
    width: `60vw`,
    height: `5.80vw`,
    backgroundRepeat: `no-repeat`,
    position: `absolute`,
    right: `0px`,
    left: `0px`,
    margin:`0px auto`
  },
textNumber: {
  color: `white`,
  display: `table-cell`,
  verticalAlign: `middle`,
  textAlign: `center`,
  fontSize:_ => `min(4vw, ${vh(5)})`,
  fontFamily: `"UnityHeadlineBold", Arial`,
},
countdownWrapper:{
  display: `flex`,
  flex:1,
  alignItems: `center`,
  justifyContent: `center`,
  left: 0,
  right: 0,
  margin:`0 auto`,
  width: `100%`,
  height: `100%`,
  position:`absolute`
 },
  container: {
    // position: `absolute`,
    zIndex: 2,
    backgroundImage: `url(` + bgByteCounter + `)`,
    backgroundRepeat:`no-repeat`,
    backgroundSize: "contain",
    width: `100%`,
    height: `20vw`,
    margin: `0px auto`,
    backgroundPosition: `center`,
    position:`relative`
  },
  containerMobile: {
    zIndex: 2,
    backgroundImage: `url(` + bgByteCounter + `)`,
    backgroundRepeat:`no-repeat`,
    backgroundSize: "contain",
    width: `100%`,
    height: `40vw`,
    margin: `0px auto`,
    backgroundPosition:`center`
  },
  text: {
    color: `white`,
    textAlign: `center`,
    fontFamily: `"UnityHeadlineBold", Arial`,
    marginTop: _ => `min(${vh(6)}, 4vw)`,
    fontSize: _ => `min(${vh(2)}, 2vw)`,
    fontWeight: 'bold',
  }
}));

export { useStyles }

/*
Este fue el replace que use para transformar vh en una multiplicacion por window.innerHeight

Esto
`(.+[ \(,])?([\d\.]+)vh
por esto
_ => `$1${$2*0.01*window.innerHeight}px

*/

// From var to vh()
// calc\(([\d\.]+) \* var\(--vh\)\)
// ${vh($1)}


// Avoid unnecessary template strings
// `\$\{vh\(([\d\-]+)\)\}`
// vh($1)
