import React, { useLayoutEffect, useState } from 'react';
import { useStyles } from './styles'
import {
    mobile_background,
    web_background,
    description,
    logo,
    subtitle,
    title_mobile,
    title_web,
    web_image,
    mobile_image,
    operacion_tormenta,
    video,
    titleTemp,
    bgtemp,
    santaLeft,
    iconFemza,
    videoMobile,
    titleTempMobile,
    bgByteDesktop,
    iconByteCoca,
} from '../../assets/index'

import Countdown from '../components/countDown'
import FormCoca from '../components/FormCoca';


const Form = ({ onTimeout, dateEvent, error, earlyTime }) => {
    const classes = useStyles();

    const [landscape, setLandscape] = useState(window.innerWidth > window.innerHeight);

    useLayoutEffect(() => {
        const updateSize = () => setLandscape(window.innerWidth > window.innerHeight);
        window.addEventListener('resize', updateSize);
        updateSize();
        return () => window.removeEventListener('resize', updateSize);
    }, []);

    return (

        
        <div className={classes.container} style={{ backgroundImage:`url(`+bgByteDesktop+`)` }} >
            
            <div className={ landscape ? classes.leftContainer : classes.leftContainerMobile} style={{ backgroundImage: `url(`+iconByteCoca+`)` }} ></div>
            
                
            <div className={ landscape ? classes.rightContainer : classes.rightContainerMobile} style={{ backgroundImage: `url(`+iconFemza+`)` }} ></div>



            { !landscape ? null : null }
           
            {
                <FormCoca landscape={ landscape } />
            }
            {
                dateEvent ?
                !landscape ? <div className={ classes.footerTextMobile }></div> : <div className={ classes.footerText }></div>
                
                : null
                
            }
            {
                error
                    ? <p className={classes.error}>{error}</p>
                    : null
            }

        </div>
    )
}




export default Form