import React, { useEffect, useState } from "react";
import { Form } from '../features/index'
import { finished as FinishedImage } from '../assets'
import '../index.css'
import { makeStyles } from '@material-ui/core/styles';

const FormScreen = () => {
    
    const useStyles = makeStyles((theme) => ({
        finishedBanner: {
            backgroundImage: `url("${FinishedImage}")`,
            width: "100vw",
            height: "100vh",
            backgroundSize: "contain",
            backgroundPosition: "center",
            backgroundRepeat: "no-repeat"
        }
    }));
      
    const [timedOut, setTimedOut] = useState(false);
    const [fullscreen, setFullscreen] = useState(false);
    const [dateEvent, setDateEvent] = useState(null);
    const [earlyTime, setEarlyTime] = useState(null);
    const [finished, setFinished] = useState(false);
    const [error, setError] = useState(null);
    
    const classes = useStyles();
    
    useEffect(() => {
            //window.location.pathname.replace(/\/$/, "") + "/event.json"
        fetch("/event.json")
            .then(res => res.json())
            .then(dataResult => {
    
                let data = dataResult.events[0];

                console.log("DATA: ",data);
            
                if (data.end_time) {
                    let diff = new Date(data.end_time).getTime() - Date.now();
                    console.log(data.end_time, " -- ", window.location.pathname.replace(/\/$/, "") + "/event.json")
                    if (diff <= 0) return setFinished(true)
                }
    
                let diff = new Date(data.date).getTime() - Date.now() - 1000 * (data.early_time || 0);
                if (data.fullscreen == true) setFullscreen(true);
                if (diff <= 0) return setTimedOut(true)
            
                setEarlyTime(data.early_time);
                setDateEvent(data.date);
            
            })
            .catch(err => {
                console.error(err);
                setError("Ha habido un error inesperado. Prueba de nuevo ms tarde")
            })
    
    }, []);
    
    if (finished) {
        return <div className={classes.finishedBanner}></div>
    }
    
    
    
    return (
        <Form onTimeout={() => setTimedOut(true)} timedOut={timedOut} dateEvent={dateEvent} error={error} earlyTime={earlyTime} />
    );
}

export default FormScreen