import React, { useState, useEffect, useRef } from 'react'
import { useStyles } from './styles'
import axios from 'axios'
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';


const FormCoca = ({landscape}) => { 
    
    const classes = useStyles();

    const token = 'SoRtEoS@2022';

    const [enableform, setEnableform] = React.useState(true);
    const [validated, setValidated] = useState(false);

    const [docket, setDocket] = React.useState('');
    const [name, setName] = React.useState('');
    const [lastname, setLastname] = React.useState('');
    const [area, setArea] = React.useState([]);
    const [distributor, setDistributor] = React.useState('');

    const [currentarea, setCurrentarea] = React.useState('');
    const [currentdistributor, setCurrentdistributor] = React.useState('');

    const [showMessage, setShowMessage] = React.useState(false);
    const [messageStyle, setMessageStyle] = React.useState('');
    const [messageText, setMessageText] = React.useState('');

    React.useEffect(() => {
        
        GetDropDownData();
        
    }, [enableform]);

    const GetDropDownData = async () => {
        try {
            const json = await axios.get("https://cocacola.trazzart.com/varios/v1/sorteos/variables",
                {
                    headers:
                        { "Authorization": `Bearer ${token}` }
                });

            setArea([]);
            setDistributor([]);
            setArea(json.data.areas);
            setDistributor(json.data.distribuidores);
            setEnableform(false);
        } catch (error) {
            console.log(error)
          }
    }

    const SendForm = async (e) => {
        e.preventDefault();

        const form = e.currentTarget;

        if (form.checkValidity() === false) {
            e.preventDefault();
            e.stopPropagation();
        } else { 
            SendDataToServer();
        }

        setValidated(true);

    }

    const SendDataToServer = async (e) => { 
        try {

            const json = { 
                legajo: docket,
                nombre: name,
                apellido: lastname,
                area:currentarea,
                distribuidor:currentdistributor
            };
            

            const res = await axios.post("https://cocacola.trazzart.com/varios/v1/sorteos/inscripcion", json,
                {
                    headers:
                    {
                        'Content-Type': 'application/json',
                        "Authorization": `Bearer ${token}`,
                    }
                });

            if (res.data.res == 'error') {
                setMessageStyle('danger');
            } else { 
                setMessageStyle('success');
            }
            setMessageText(res.data.desc);
            setShowMessage(true);
            setTimeout(() => {
                setShowMessage(false);
            }, 3000);

            setDocket('');
            setName('');
            setLastname('');
            setCurrentarea('');
            setCurrentdistributor('');
            setValidated(false);

          } catch (error) {
            console.log(error)
          }
    }
    
    return (
        <div className={classes.formContainer }>
            <Container className={classes.formContainer }>
                <Alert className={ classes.alertMessage } variant={messageStyle} show={ showMessage } onClose={() => setShowMessage(false)} dismissible >
                     <p>{ messageText }</p>
                </Alert>

                <Form className={landscape ? classes.formcoca : classes.formcocaMobile} noValidate validated={validated} onSubmit={SendForm} >
                <Form.Group className="mb-3">
                        <Form.Label className={classes.forLabel}>Area</Form.Label>
                        <Form.Select disabled={enableform} onChange={ e=> setCurrentarea(e.target.value) } value={ currentarea } required >
                        {
                                !enableform ? ( <option selected disabled value="">Seleccione su rea</option> ) : null
                            }
                            {
                                !enableform ?
                                
                                area.map(i => (
                                    <option
                                    key={i}
                                    value={i}
                                    >{i}
                                    </option>
                                ))
                                :
                                (<option value="0">Cargando...</option>)
                            }
                        </Form.Select>
                        <Form.Control.Feedback type="invalid">
                            Seleccione su rea por favor.
                        </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label className={classes.forLabel}>Distribuidor</Form.Label>
                        <Form.Select disabled={enableform} onChange={e => setCurrentdistributor(e.target.value)} value={ currentdistributor } required >
                            {
                                !enableform ? ( <option selected disabled value="">Seleccione su distribuidor</option> ) : null
                            }
                            {
                                !enableform ?
                                
                                distributor.map(i => (
                                    <option
                                    key={i}
                                    value={i}
                                    >{i}
                                    </option>
                                ))
                                :
                                (<option value="0">Cargando...</option>)
                            }
                        </Form.Select>
                        <Form.Control.Feedback type="invalid">
                            Seleccione su Distribuidor por favor.
                        </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="validationCustom01">
                        <Form.Label className={classes.forLabel}>Legajo</Form.Label>
                        <Form.Control value={docket} onChange={e => { setDocket(e.target.value) }} type="text" className='form-control' placeholder="Ingrese su legajo" disabled={enableform} required ></Form.Control>
                        <Form.Control.Feedback type="invalid">
                            Ingrese su legajo por favor.
                        </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label className={classes.forLabel} >Nombre</Form.Label>
                        <Form.Control value={name} onChange={e => { setName(e.target.value) }} type="text" className='form-control' placeholder="Ingrese su nombre" disabled={enableform} required ></Form.Control>
                        <Form.Control.Feedback type="invalid">
                            Ingrese su Nombre por favor.
                        </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label className={classes.forLabel}>Apellido</Form.Label>
                        <Form.Control value={lastname} onChange={e => { setLastname(e.target.value) }} type="text" className='form-control' placeholder="Ingrese su apellido" disabled={enableform} required ></Form.Control>
                        <Form.Control.Feedback type="invalid">
                            Ingrese su Apellido por favor.
                        </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        
                        <Button type="submit" disabled={ enableform }>Enviar</Button>

                    </Form.Group>
                </Form>
            </Container> 
        </div>
    );
}

export default FormCoca