import React, { useState, useEffect, useRef } from 'react'
import { useStyles } from './styles'

const CountDown = ({landscape, dateExpiryty, onTimeout, earlyTime}) => {
  let interval = useRef(null);
  const [countDate, setCountDate] = useState({
    days: 0,
    hours: 0,
    min: 0,
    sec: 0,
  })
  
  const classes = useStyles();

  const resetState = () => {
    setCountDate({
      days: 0,
      hours: 0,
      min: 0,
      sec: 0,
    });
    stop()
  }

  const initCount = () => {
    interval.current = setInterval(() => {
      const date = calculateCountdown(dateExpiryty);
      date ? setCountDate(date) : resetState()
    }, 1000);

    setCountDate(calculateCountdown(dateExpiryty) || ((a) => a));
  }

  const calculateCountdown = (endDate) => {
    let diff = (Date.parse(new Date(endDate)) - Date.parse(new Date())) / 1000;

    // clear countdown when date is reached
    if (diff - earlyTime <= 0) return false;

    const timeLeft = {
      years: 0,
      days: 0,
      hours: 0,
      min: 0,
      sec: 0
    };

    // calculate time difference between now and expected date
    if (diff >= (365.25 * 86400)) { // 365.25 * 24 * 60 * 60
      timeLeft.years = Math.floor(diff / (365.25 * 86400));
      diff -= timeLeft.years * 365.25 * 86400;
    }
    if (diff >= 86400) { // 24 * 60 * 60
      timeLeft.days = Math.floor(diff / 86400);
      diff -= timeLeft.days * 86400;
    }
    if (diff >= 3600) { // 60 * 60
      timeLeft.hours = Math.floor(diff / 3600);
      diff -= timeLeft.hours * 3600;
    }
    if (diff >= 60) {
      timeLeft.min = Math.floor(diff / 60);
      diff -= timeLeft.min * 60;
    }
    timeLeft.sec = diff;

    return timeLeft;
  }

  const addLeadingZeros = (value) => {
    value = String(value);
    while (value.length < 2) {
      value = '0' + value;
    }
    return value;
  }

  const stop = () => {
    clearInterval(interval.current);
    onTimeout();
  }

  useEffect(() => {
    initCount()
    // return(() => {
    //   stop()
    // })
  }, [])

  return (
    <div className={ landscape ? classes.container : classes.containerMobile}>
      <div className={classes.countdownWrapper}>
        <div className={ landscape ? classes.outer : classes.outerMobile}>
          <div className={classes.inner}>
            <span className={classes.textNumber}>{addLeadingZeros(countDate.days)}</span>
          </div>
          <p className={classes.text}>Das</p>
        </div>
        <div className={ landscape ? classes.outer : classes.outerMobile}>
          <div className={classes.inner}>
            <span className={classes.textNumber}>{addLeadingZeros(countDate.hours)}</span>
          </div>
          <p className={classes.text}>Horas</p>
        </div>
        <div className={ landscape ? classes.outer : classes.outerMobile}>
          <div className={classes.inner}>
            <span className={classes.textNumber}>{addLeadingZeros(countDate.min)}</span>
          </div>
          <p className={classes.text}>Minutos</p>
        </div>
        <div className={ landscape ? classes.outer : classes.outerMobile}>
          <div className={classes.inner}>
            <span className={classes.textNumber}>{addLeadingZeros(countDate.sec)}</span>
          </div>
          <p className={classes.text}>Segundos</p>
        </div>
      </div>
      <div className={ landscape ? classes.bottomTextContainer : classes.bottomTextContainerMobile}>
        <div className={ landscape ? classes.bottomText : classes.bottomTextMobile}></div>
      </div>
    </div>
  )
}


export default CountDown